import { Card, CardContent, CardHeader, Grid, Stack } from '@mui/material';
import type { FC } from 'react';
import ReportFetcherCard from './ReportFetcherCard';
import REPORTS from './reports';
const ReportFetcher: FC = () => <Stack spacing={2} data-sentry-element="Stack" data-sentry-component="ReportFetcher" data-sentry-source-file="ReportFetcher.tsx">
    {REPORTS.map(section => <Card key={section.title}>
        <CardHeader title={section.title} />
        <CardContent>
          <Grid container spacing={2}>
            {Object.entries(section.reports).map(([reportId, report]) => <Grid key={report.title} item md={4} sm={6} xl={3} xs={12}>
                <ReportFetcherCard report={report} reportId={reportId} />
              </Grid>)}
          </Grid>
        </CardContent>
      </Card>)}
  </Stack>;
export default ReportFetcher;